.Venus .slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.Venus .slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.Venus .slick-list:focus {
  outline: none;
}

.Venus .slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.Venus .slick-slider .slick-track,
.Venus .slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.Venus .slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.Venus .slick-track:before, .Venus .slick-track:after {
  content: "";
  display: table;
}

.Venus .slick-track:after {
  clear: both;
}

.slick-loading .Venus .slick-track {
  visibility: hidden;
}

.Venus .slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .Venus .slick-slide {
  float: right;
}

.Venus .slick-slide img {
  display: block;
}

.Venus .slick-slide.slick-loading img {
  display: none;
}

.Venus .slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .Venus .slick-slide {
  display: block;
}

.slick-loading .Venus .slick-slide {
  visibility: hidden;
}

.slick-vertical .Venus .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.Venus.Venus {
  color: #5B6D7C;
