  line-height: 1.2;
  text-transform: uppercase;
  transition: color 0.25s, border-radius 0.25s, background 0.25s, box-shadow 0.25s;
}

.Venus .btn:hover {
  border-color: #14D7AC;
  background: #14D7AC;
}

.Venus .btn:disabled {
  border-color: rgba(91, 109, 124, 0);
  background: rgba(91, 109, 124, 0.4);
  box-shadow: none;
  cursor: not-allowed;
}

.Venus .btn_secondary {
