.Venus .v-btn-nav {
  display: none;
  position: absolute;
  top: 50%;
  right: 0;
  width: 42px;
  height: 36px;
  transform: translate3d(0, -50%, 0);
  transition: opacity 0.25s;
}

@media only screen and (max-width: 767px) {
  .Venus .v-btn-nav {
    display: block;
  }
}

.Venus .v-btn-nav.is-active {
  opacity: 0.5;
}

.Venus .v-btn-nav.is-active:hover {
  opacity: 1;
}

.Venus .v-btn-nav.is-active:before {
  transform: translate3d(-3px, -1px, 0) rotate(-45deg);
}

.Venus .v-btn-nav.is-active:after {
  transform: translate3d(-3px, 1px, 0) rotate(45deg);
}

.Venus .v-btn-nav.is-active span {
  opacity: 0;
  transform: translate3d(25%, -50%, 0) scale(0.5);
}

.Venus .v-btn-nav:before, .Venus .v-btn-nav:after,
.Venus .v-btn-nav span {
  position: absolute;
  right: 10px;
  border-bottom: 2px solid #5B6D7C;
  width: 22px;
}

.Venus .v-btn-nav:before, .Venus .v-btn-nav:after {
  content: '';
  transform: translate3d(0, 0, 0);
  transition: transform 0.25s;
}

.Venus .v-btn-nav:before {
  top: 10px;
  transform-origin: right bottom;
}

.Venus .v-btn-nav:after {
  bottom: 10px;
  transform-origin: right top;
}

.Venus .v-btn-nav span {
  top: 50%;
  transform: translate3d(0, -50%, 0);
  transform-origin: right center;
  transition: opacity 0.25s, transform 0.25s;
}

